import LocalStorageUtil from "../../utils/localStorageUtil";
import Error from '../Error';
import { ROOT_MODAL,ROOT_INDEX } from "../../constants/root";
import './App.css';
import Modal from '../Modal';
import Note from '../Note';
import Spinner from '../Spinner'
class App{
    render() {
        try{
            Spinner.render()
            setTimeout(() => {
                ROOT_INDEX.innerHTML = '<button id="btn" class="btn btnForCreateNote">Create note</button>';
                LocalStorageUtil.renderLoading();
                Note.render();
                this.eventListener();
                Spinner.handleClear();
            },500)
        } 
        catch(error) { //  
            ROOT_INDEX.innerHTML = '';
            Error.render();
            console.log(error.message, error.fileName, error.lineNumber);
        }; 
    }; 
    eventListener() {
        document.addEventListener('click', (event) => {
            let target = event.target.closest('#btn');
            if(!target) return;
            Note.render();
        })


        document.addEventListener('click', (event) =>{
            let target = event.target.closest('.card__item');
            if(!target) return;
            let content = target.lastElementChild.textContent.trim(); //  
            // content = content.replaceAll('<br>', '')
            // console.log(content)
            Modal.open(content);

             document.querySelector('#confirmer').addEventListener('click', function() { //       
                target.lastElementChild.textContent = textarea.value; //       textarea
                Modal.close()
            })
        })

        document.addEventListener('keydown', (event) => {
            if(event.code.toLowerCase() == 'escape') {
                Modal.close()
            }
        })

        document.addEventListener('click', (event) => { //  
            let target = event.target.closest('.close');
            if(!target) return;
            target.parentNode.parentNode.innerHTML = ''; //         
            ROOT_MODAL.innerHTML = ''; //    
        })

        document.addEventListener('click', (event) => { //   
            let target = event.target;
            if(target.dataset.close) {
                Modal.close()
            };
        });

        window.onbeforeunload = () => { //           localStorage
            let cards = document.querySelectorAll('.card__item'); //   
            LocalStorageUtil.clear(); //  
            for(let card of cards) {
                LocalStorageUtil.putNotes(card.id, card.lastElementChild.textContent.trim()); //      
            };
        };
    };
};

export default new App();

